.Splash-icon{
    width: 80px;
    height: 80px;
    background-image: url('../../assets/img/google.png');
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
}
.center {
    margin: auto;
    width: 50%;
    max-width: 400px;
    min-width: 150px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
  }
  